.ssh {
    font-family: Source Han Sans CN;

    .card-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 0.26rem;

        .title {
            margin-left: 0.30rem;

            .main-title {
                font-size: 0.18rem;
                font-weight: 400;
                line-height: 0.18rem;
                color: #040E34;

            }

            .lat-title {
                // display: inline-block;
                font-size: 0.12rem;
                //line-height: 0.12rem;
                color: #9696A3;
                margin-top: 0.08rem;
            }
        }


        .create-btn {
            width: 1.06rem;
            height: 0.4rem;
            background: #5C5EE4;
            border-radius: 4px;
            color: #FFFFFF;
            font-size: 0.14rem;
            margin-right: 0.4rem;
            display: flex;
            justify-content: center;
            align-items: center;

            .create-icon {
                width: 0.14rem;
                margin-right: 0.08rem;
            }
        }


    }

    .card-main {
        margin-top: 0.18rem;

        .ant-table-thead>tr>th {
            background: #F2F2FD;
            color: #9696A3;
            font-size: 0.13rem;
            line-height: 1;
            padding: .13rem 0 .13rem .3rem;
        }

        // .ant-table-thead>tr>th {
        //     background: #F2F2FD;
        //     color: #9696A3;
        //     height: 0.13rem;
        //     font-size: 0.13rem;
        //     font-weight: 400;
        //     padding-left: 0.3rem;
        // }

        .ant-table-tbody>tr>td {
            padding: 0 0 0 0.3rem;
            height: 0.6rem;
            font-size: 0.14rem;
        }

        .ant-table-cell-row-hover{
            background: #F0F0FD !important;
        }

        .ant-table-cell::before {
            height: 0px !important;
        }

        .column-name>td {
            padding-left: 20px;
        }

        .colum-operation>tr>td {
            padding-right: 20px;
        }

        .more {
            color: #5C5EE4;
            font-size: 0.18rem;
            cursor: pointer;


        }

        .more-operation {
            display: flex;
            align-items: center;

            // display: none;
            .more-edit,
            .more-delete {
                display: inline;
                font-size: 0.14rem;
                color: #5C5EE4;
            }

            .more-delete {
                margin-left: 0.2rem;
            }
        }
    }

    .modal-create-ssh {
        .modal-title {
            font-size: 0.18rem;
            font-weight: 400;
            line-height: 0.28rem;
            color: #040E34;
            margin-bottom: 0.32rem;
        }

        .modal-form {
            overflow: hidden;

            .status {
                color: #42C2FB;
                height: 0.14rem;
                font-size: 0.14rem;
            }

            .ant-input {
                background: #F2F2F7;
            }

            .modal-form-footer {
                width: 100%;
                justify-content: center;
                font-size: 0.14rem;
                letter-spacing: 0;

                .btn-ok,
                .btn-no {
                    border-radius: 4px;
                }

                .btn-ok {
                    background: #5C5EE4;
                    color: #ffffff;
                }

                .btn-no {
                    border: 1px solid #D9DBE1;
                    margin-left: 0.08rem;
                }
            }
        }
    }

}

.swal2-popup {
    font-size: 0.2rem;
}

.ssh-cancel-button {
    background: #fff !important;
    color: #525666 !important;
    border: 1px solid #D9DBE1 !important;
}

.ssh-icon {
    color: #F64E60 !important;
    border-color: #F64E60 !important;
}
@primary-color: #5C5EE4;